@import "../../common/variables";
@import "../../common/mixins";

.header {

  &__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    min-height: 100px;
    border-bottom: 1px solid #f2f2f2; }

  &__logo {
    display: block;
    width: 190px;
    line-height: 0; }

  &__img {
    width: 100%; }

  &__address {
    flex-basis: 21%;
    position: relative;
    padding-left: 20px;
    font-family: $font-family-third;

    &::before {
      content: url("./maps-and-flags.png");
      position: absolute;
      left: 0; } }

  &__mail {
    flex-basis: 15%;
    position: relative;
    padding-left: 20px;
    font-family: $font-family-third;
    letter-spacing: -0.04em;

    &::before {
      content: url("./envelope.png");
      position: absolute;
      left: 0; } }

  &__phone {
    color: #010101; }

  &__namber {
    font-size: 18px;
    font-weight: 700; }

  &__grapfic {
    font-size: 10px;
    font-weight: 600; }

  &__button {
    display: block;
    color: #1fba0a;
    padding: 12px 18px;
    font-size: 13px;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
    border-radius: 5px;
    border: 1px solid #1fba0a;
    background-color: #ffffff;
    transition: 0.3s;

    &:hover {
      background-color: #1fba0a;
      color: #ffffff; } }

  &__menu {
    display: flex;
    justify-content: space-between; }

  &__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 800px; }

  &__link {
    display: block;
    padding: 18px 29px;
    font-weight: 600;
    text-transform: uppercase;
    text-decoration: none;
    color: #000000;
    letter-spacing: 0.7px;
    transition: 0.2s;

    &:hover {
      background-color: $color-green;
      color: #ffffff; }

    svg {
      width: 8px;
      margin: 0 0 -2px 2px; } }

  &__home {
    display: flex;

    a {
      display: block;
      padding: 17px 29px 17px 55px;
      border: 1px solid transparent;
      font-weight: 600;
      text-transform: uppercase;
      text-decoration: none;
      color: #000000;
      letter-spacing: 0.7px;
      transition: 0.3s;

      &:hover {
        color: #21ba0b;
        border: 1px solid #21ba0b; } } }

  &__user {
    position: relative;
    padding-left: 10px;

    &::before {
      content: url("user.png");
      position: absolute;
      left: 17px; } }


  &__basket {
    position: relative;
    padding-left: 10px;

    &::before {
      content: url("cart.png");
      position: absolute;
      left: 17px; }

    &::after {
      content: "2";
      position: absolute;
      left: 34px;
      top: 14px;
      width: 11px;
      height: 11px;
      background-color: $color-button;
      color: #ffffff;
      font-size: 7px;
      border-radius: 3px;
      text-align: center; } } }


