@import "../../common/variables";
@import "../../common/mixins";

html {
  box-sizing: border-box;
  font-family: $font-family-base;
  font-size: $font-size-base;
  color: $color-text-base; }

*, *:before, *:after {
  box-sizing: inherit; }

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal; }

ol, ul {
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }

* {
  margin: 0;
  padding: 0; }

html,
body {
  height: 100%; }
