// VARIABLE

// FONT FAMILY
$font-family-base: 'OpenSans', sans-serif;
$font-family-second: 'LemonTuesday', sans-serif;
$font-family-third: 'MyriadPro', sans-serif;
$font-family-fourth: 'RobotoSlaBbold', sans-serif;
$font-family-fifth: 'OpenSansBold', sans-serif;

$font-weight-thin: 100;           // Thin (Hairline)
$font-weight-extra-light: 200;    // Extra Light (Ultra Light)
$font-weight-light: 300;          // Light
$font-weight-normal: 400;         // Regular (normal)
$font-weight-medium: 500;         // Medium
$font-weight-semi-bold: 600;      // Semi Bold (Demi Bold)
$font-weight-bold: 700;           // Bold
$font-weight-extra-bold: 800;     // Extra Bold (Ultra Bold)
$font-weight-black: 900;          // Black (Heavy)

// COLOR
$color-background: #fff;
$color-text-base: #000;
$color-button: #fd4f10;
$color-title: #32505a;
$color-green: #84c604;

// FONT SIZE
$font-size-base: 14px;
$font-size-button: 16px;
$h1: $font-size-base * 3;
$h2: $font-size-base * 2;
//$h3: $base-font-size * 1.5
//$h4: $base-font-size * 1.3
//$h5: $base-font-size * 1.2
//$h6: $base-font-size * 1.1

$desktop_lg: 2250px;
$desktop_md: 1920px;
$desktop_sm: 1700px;
$laptop_lg: 1500px;
$laptop_md: 1300px;
$laptop_sm: 1199px;
$tablet: 991px;
$mobile_lg: 767px;
$mobile_md: 639px;
$mobile_sm: 479px;
$mobile_xs: 360px;
